*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

::-ms-input-placeholder {
  color: gray;
}

::placeholder {
  color: gray;
}

::selection {
  color: #fff;
  background-color: #28a745;
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

ul, ol {
  margin: 0;
  padding-left: 0;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

body {
  color: #333;
  background-color: #fff;
  font-family: Open Sans, sans-serif;
  font-size: 18px;
  line-height: 1.55;
}

.btn {
  min-width: 165px;
  color: #fff;
  fill: #fff;
  background-color: #28a745;
  border: 1px solid #28a745;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  padding: 10px 28px;
  line-height: 1.55;
  text-decoration: none;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), border-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-flex;
}

.btn:hover, .btn:focus {
  background-color: #228e3b;
  border-color: #228e3b;
}

.btn__text + .btn__icon, .btn__icon + .btn__text {
  margin-left: 6px;
}

.btn--100 {
  width: 100%;
}

.btn--sm {
  padding: 7px 14px;
  line-height: 1.4;
}

.btn--secondary {
  background-color: #0284d0;
  border-color: #0284d0;
}

.btn--secondary:hover, .btn--secondary:focus {
  background-color: #0270b1;
  border-color: #0270b1;
}

.btn--outline {
  background-color: rgba(0, 0, 0, 0);
  border-color: #fff;
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 30px;
  padding-right: 30px;
}

.title {
  font-size: 38px;
  font-weight: 300;
  line-height: 1.26;
}

.title--light {
  color: #fff;
}

.title--dark {
  color: #333;
}

.title--large {
  font-size: 55px;
  line-height: 1.2;
}

.top-text {
  font-size: 20px;
  font-weight: normal;
  line-height: 1.65;
}

.top-text--light {
  color: #fff;
}

.top-text--dark {
  color: #333;
}

.top-text--large {
  font-size: 22px;
  font-weight: normal;
  line-height: 1.67;
}

.overlay {
  opacity: 0;
  visibility: hidden;
  width: 100%;
  height: 100%;
  z-index: 20;
  background-color: rgba(0, 0, 0, .5);
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  opacity: 0;
  visibility: hidden;
  width: 100%;
  max-width: 500px;
  z-index: 30;
  text-align: center;
  background-color: #fff;
  border-radius: 3px;
  padding: 30px;
  transition: all .3s;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 3px 10px -.5px rgba(0, 0, 0, .2);
}

.modal.active, .overlay.active {
  opacity: 1;
  visibility: visible;
}

.modal__cross {
  width: 15px;
  height: 15px;
  fill: #444;
  cursor: pointer;
  position: absolute;
  top: 20px;
  right: 20px;
}

.header {
